*{font-family:"Alata", sans-serif}body{background:linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);background-size:1800% 1800%;-webkit-animation:gradient 10s ease infinite;animation:gradient 10s ease infinite}#color-mode{display:none}@-webkit-keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.navbar{height:60px;background:transparent;width:100%}.logo{font-weight:800;font-size:20px;border-bottom:solid 1px black}li{font-weight:500;font-size:20px;text-decoration:none;list-style:none}li:hover{color:white;cursor:pointer}ul{display:flex;justify-content:space-between;align-items:center}.nav_links li{display:inline-block}#audio{size:8px;position:absolute;bottom:20px}#render{position:absolute;top:0;z-index:-1}#demo{font-weight:bold}#demo:hover{cursor:pointer}#music{background-color:transparent;border-radius:6px;border:1px solid transparent;display:inline-block;cursor:pointer;color:black;font-family:Arial;font-weight:bold;padding:0;text-decoration:none;font-size:45px;border:none;outline:none}#music:hover{color:white}#music:active{border:none;outline:none}button{font:inherit;background:transparent;border:3px solid black;padding:0.5rem 1rem;color:black;cursor:pointer;font-weight:bold;border-radius:3px}button:hover,button:active{border-color:black;color:white}.modal{display:block;position:fixed;z-index:100;top:30%;left:35%;width:25%;background:linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);background-size:400% 400%;-webkit-animation:gradient 10s ease infinite;animation:gradient 10s ease infinite;padding:40px;text-align:center}.header-text{text-align:center;font-size:40px}button.btn-close:hover{color:white;border-color:black}#three{height:65px;width:135px}.modal-close{padding-top:10px;text-align:right;display:flex;justify-content:space-between}button.btn-cancel{border-color:black;color:black}.modal-background{display:block;position:fixed;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.4);z-index:100}.open{display:block}.intro{border-bottom:1px solid black;padding-bottom:15px;font-size:20px}
